export default {
	data() {
		return {
			showService: false
		};
	},
	methods: {
		goRedPackage() {
			uni.navigateTo({
				url: './myRedPackage/myRedPackage'
			})
		},
		goMyAddress() {
			uni.navigateTo({
				url: './myAddress/myAddress'
			})
		},
		goMyOrder() {
			uni.navigateTo({
				url: './myOrder/myOrder'
			})
		},
		goMyEnergy() {
			uni.navigateTo({
				url: './myEnergy/myEnergy'
			})
		},
		goMyService() {
			this.showService = true
		},
		canService () {				this.showService = false
		},
		goMyRule() {
			uni.navigateTo({
				url: './myRule/myRule'
			})
		}
	}
}
